.ant-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.ant-loader-inner {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.ant-loader-dot {
  position: absolute;
  top: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #1890ff;
  animation: ant-loader 1.4s ease-in-out infinite both;
}

.ant-loader-dot:nth-child(1) {
  left: 6px;
  animation-delay: -0.24s;
}

.ant-loader-dot:nth-child(2) {
  left: 6px;
  animation-delay: -0.12s;
}

.ant-loader-dot:nth-child(3) {
  left: 26px;
  animation-delay: 0;
}

@keyframes ant-loader {
  0%,
  80%,
  100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}
