.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.featuredItem1 {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  background-color: #fff3e0;
  /* background-image: linear-gradient(36deg,#A100FFFF,#119CFDFF); */
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}
.featuredItem2 {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  background-color: #ddfde8;
  /* background-image: linear-gradient(36deg,#A100FFFF,#119CFDFF);  cursor: pointer; */
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}
.featuredItem3 {
  flex: 1;
