}

.featuredTitle{
    font-size: 23px;
    font-weight: bold;
    color: black;
}

.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredIcon{
    font-size: 14px;

    margin-left: 5px;
    color: rgb(10, 236, 10);
}

.featuredIcon.negative{
    color: red;
}

.featuredSub{
    font-size: 15px;
    color: rgb(139, 135, 135);
}