.sidebar {
  flex: 1;
  height: calc(100vh - 50px);
  background-color: white;
  position: sticky;
  top: 50px;
}

.sidebarWrapper {
  padding: 20px;
  color: #000000;
}

.sidebarMenu {
  margin-bottom: 10px;

}

.sidebarTitle {
  font-size: 20px;
  color: black;
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  color: rgb(6, 6, 6);
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color:rgba(0, 97, 255, 0.2);
}

.sidebarIcon{
    margin-right: 7px;
    font-size: 25px !important;
}
